import { Box, Button, Image, Text } from "@chakra-ui/react";
import React from "react";

const About = () => {
  return (
    <Box m="auto" >
          <Box
              pt={14}
        display="flex"
        bgColor={"#EBF7FC"}
        flexDirection={[
          "column",
          "column",
          "column",
          "row",
          "row",
          "row",
        ]}
        colGap={10}
        justifyContent="center"
      >
        <Box width={["full", "full", "full", "full", "30%", "30%"]} >
          <Image
            src={"https://avatars.githubusercontent.com/u/110052834?v=4"} rounded="50%" m={"auto"}
          />
        </Box>
        <Box
          width={["full", "full", "full", "full", "60%", "60%"]}
          display="flex"
          alignItems={"center"}
        >
          <Box
            p={10}
            display="flex"
            flexDir={"column"}
            justifyContent="center"
            m={"auto"}
          >
            <Text
              lineHeight={1}
              fontWeight={600}
              fontSize={{ base: "4xl", sm: "5xl", lg: "7xl" }}
              color={"#211650"}
              textAlign="center"
            >
              About Me
            </Text>
            <Text fontSize={"xl"} pt={4} color="#E97D20" fontWeight={"500"}>
            An inspired team player, always curious about new problems and learning new things to deliver an out-of-the-box solution with complete ownership. I hope to work in a challenging environment as a specialized MERN stack developer.
            </Text>
            
          </Box>
        </Box>
      </Box>
    </Box>
  );
};

export default About;
