import {
  Box,
  Button,
  FormControl,
  FormLabel,
  Image,
  Input,
  Text,
  Textarea,
} from "@chakra-ui/react";
import React from "react";
import mail from "../utils/mail.png";
const Contact = () => {
  return (
    <Box m="auto">
      <Box
        py={10}
        display="flex"
        flexDir={"column"}
        justifyContent="center"
        m={"auto"}
        bgColor={"#EBF7FC"}
      >
        <Text
          lineHeight={1}
          fontWeight={600}
          fontSize={{ base: "4xl", sm: "5xl", lg: "7xl" }}
          color={"#211650"}
          textAlign="center"
        >
          Contact Me
        </Text>
      </Box>
      <Box
        display="flex"
        bgColor={"#EBF7FC"}
        flexDirection={["column", "column", "column", "row", "row", "row"]}
        colGap={10}
      >
        <Box
          width={["full", "full", "full", "full", "50%", "50%"]}
          display="flex"
          alignItems={"center"}
          flexDir={"column"}
          px={24}
          justifyContent="center"
        >
          <Box display="flex" width="100%" m={"auto"} justifyContent="center">
            <form
              style={{
                display: "flex",
                width: "100%",
                flexDirection: "column",
                maxWidth: "400px",
              }}
            >
              <FormControl>
                <FormLabel htmlFor="name">Name</FormLabel>
                <Input
                  type="text"
                  id="name"
                  placeholder="Enter your name"
                  style={{
                    border: "1px solid #0052cc",
                    color: "#0052cc",
                  }}
                />
              </FormControl>
              <FormControl>
                <FormLabel htmlFor="email">Email</FormLabel>
                <Input
                  type="email"
                  id="email"
                  placeholder="Enter your email"
                  style={{
                    border: "1px solid #0052cc",
                    color: "#0052cc",
                  }}
                />
              </FormControl>
              <FormControl>
                <FormLabel htmlFor="message">Message</FormLabel>
                <Textarea
                  id="message"
                  placeholder="Enter your message"
                  style={{
                    border: "1px solid #0052cc",
                    color: "#0052cc",
                  }}
                />
              </FormControl>
              <FormControl mt={4}>
                <Input
                  type={"submit"}
                  value="Send"
                  bg={"#909EFF"}
                  fontWeight="bold"
                />
              </FormControl>
            </form>
          </Box>
        </Box>
        <Box
          width={["full", "full", "full", "full", "50%", "50%"]}
          display="flex"
          alignItems={"center"}
        >
          <Image src={mail} width="100%" />
        </Box>
      </Box>
    </Box>
  );
};

export default Contact;
